.scroll-to-top {
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
  -webkit-animation: fadeIn 700ms ease-in-out 1s both;
          animation: fadeIn 700ms ease-in-out 1s both;
  cursor: pointer;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


